<template>
  <div class="template-container">
    <div class="template-item-wrap">
      <div class="temp-item-inlineblock">
        <el-select v-model="productParams.product_type" filterable placeholder="请选择">
          <el-option v-for="item in PRODUCT_REFS_TYPE_list" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div class="temp-item-inlineblock">
        <el-input v-model="productParams.product_name" clearable placeholder="标的物名称"></el-input>
      </div>
      <div class="temp-item-inlineblock">
        <el-input v-model="productParams.product_refs_id" clearable placeholder="标的物id"></el-input>
      </div>
      <!-- <div class="temp-item-inlineblock">
        <el-input v-model="productParams.account_id" clearable placeholder="广点通账号ID"></el-input>
      </div> -->
      <div class="temp-item-inlineblock">
        <el-button type="primary" @click="searchList">查询</el-button>
      </div>
      <div class="temp-item-inlineblock temp-item-right">
        <el-button type="primary" @click="newProduct">新建标的物</el-button>
      </div>
    </div>
    <div class="table-wrap">
      <el-table v-loading="loading" :data="tableData" style="width: 100%" :default-sort="{prop: 'date', order: 'descending'}">
        <el-table-column prop="product_name" label="标的物名称" sortable>
        </el-table-column>
        <el-table-column prop="product_refs_id" label="标的物ID" sortable>
        </el-table-column>

        <!-- <el-table-column prop="" label="广点通账号ID">
          <template slot-scope="scope">
            <span v-if="scope.row.account_id=='0'">
              通用
            </span>
            <span v-else>
              {{scope.row.account_id}}
            </span>
          </template>
        </el-table-column> -->
        <el-table-column prop="product_type" label="标的物类型" sortable :formatter="setProduct_type">
        </el-table-column>
        <el-table-column prop="create_time" label="创建时间" sortable>
        </el-table-column>
      </el-table>
      <div class="table-pagination" v-if="page_info.total_number>0">
        <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="page_info.page" :page-sizes="[10, 20, 30, 40]" :page-size="page_info.page_size" layout="total, sizes, prev, pager, next, jumper" :total="page_info.total_number">
        </el-pagination>
      </div>
    </div>
  </div>

</template>

<script>
export default {
  created: function() {
    this.getEnumList()
    if (this.account_id) {
      this.getList(this.productParams)
    }
  },
  watch: {
    // 监听 account_id 发生变化
    account_id() {
      this.getList(this.productParams)
    }
  },
  computed: {
    account_id() {
      var account = this.$store.getters.get_account_action
      return account.account_id
    }
  },
  data() {
    return {
      productParams: {
        product_type: '',
        product_name: '',
        account_id: '',
        product_refs_id: '',
        page: 1,
        page_size: 10
      },
      tableData: [],
      page_info: {},
      PRODUCT_REFS_TYPE_list: [],
      product_refs_type_list: {},
      loading : false
    }
  },
  methods: {
    // 新建
    newProduct() {
      this.$router.push('/setting/product/new')
    },

    // 分页
    handleSizeChange(val) {
      this.productParams.page_size = val
      this.getList(this.productParams)
      // console.log(`每页 ${val} 条`)
    },
    handleCurrentChange(val) {
      this.productParams.page = val
      this.getList(this.productParams)
      //console.log(`当前页: ${val}`)
    },
    // 查询
    searchList() {
      this.productParams.page = 1
      this.productParams.page_size = 10
      this.getList(this.productParams)
    },
    // 标的物类型 枚举
    setProduct_type(row, column) {
      // console.log(this.product_refs_type_list[row.product_type]['name'])
      return this.product_refs_type_list[row.product_type]['name']
    },
    // 获取标的物列表
    getList(params) {
      console.log(params)
      this.loading = true 
      params.account_id = this.account_id
      var that = this
      this.$axios
        .get('/Admin/Marketing_Products/getList', {
          params: params
        })
        .then(function(response) {
           that.loading = false 
          var respData = response.data
          if (respData.code == 0) {
            that.page_info = respData.data.page_info
            that.tableData = respData.data.list
          } else {
            that.$message(respData.msg)
          }
        })
        .catch(function(err) {
           that.loading = false 
          that.$message('网络繁忙，请稍后再试！')
          console.log(err)
        })
    },

    // 获取类型映射枚举列表
    getEnumList() {
      var that = this
      this.$axios
        .get('/Admin/Marketing_Common/getEnum', {})
        .then(function(response) {
          var respData = response.data
          if (respData.code == 0) {
            var list = respData.data['PRODUCT_REFS_TYPE']
            that.product_refs_type_list = list
            that.PRODUCT_REFS_TYPE_list.push({ value: '', label: '所有平台' })
            for (var key in list) {
              var obj = new Object()
              obj.value = key
              obj.label = list[key]['name']
              that.PRODUCT_REFS_TYPE_list.push(obj)
            }
          }
        })
        .catch(function(err) {
          console.log(err)
        })
    }
  }
}
</script>


<style scoped>

</style>



